import React,{useState, useEffect} from 'react';
import axios from 'axios';
import './top.css';
import RulerControls from './RulerControls'; //  Import your component
import UploadIcon from '@mui/icons-material/Upload';
import DownloadIcon from '@mui/icons-material/Download';
import AddIcon from '@mui/icons-material/Add';
import UndoIcon from '@mui/icons-material/Undo';
import RedoIcon from '@mui/icons-material/Redo';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import NoteAltIcon from '@mui/icons-material/NoteAlt';
import DocumentScannerIcon from '@mui/icons-material/DocumentScanner';
import HomeIcon from '@mui/icons-material/Home';
import OpenWithIcon from '@mui/icons-material/OpenWith';
import DeleteIcon from '@mui/icons-material/Delete';
import ProjectManager from '../canvas/ProjectManager';
import Tooltip from '@mui/material/Tooltip';
import RotateRightIcon from '@mui/icons-material/RotateRight'; // Right Move
import RotateLeftIcon from '@mui/icons-material/RotateLeft'; // Left Move
import FlipIcon from '@mui/icons-material/Flip'; // Mirror X
import FlipCameraAndroidIcon from '@mui/icons-material/FlipCameraAndroid'; // Mirror Y
import layoutEngine from '../libraries/layoutEngine';
import InsertLinkIcon from '@mui/icons-material/InsertLink'; // for instance
import InstanceInsertForm from './InstanceInsertForm'; //  Make sure file exists
import hierarchyManager from '../libraries/HierarchyManager';


const TopBar = () => {

  const [designName, setDesignName] = React.useState(ProjectManager.getDesignName());
  console.log(JSON.stringify(layoutEngine.getCells(), null, 2));
  const [isPopupOpen, setIsPopupOpen] = useState(false); // State to toggle popup visibility
  const [activeTool, setActiveTool] = React.useState(ProjectManager.getTool());

  // for ruler all state
  const [unit, setUnit] = useState("1");
  const [drawMode, setDrawMode] = useState("two");
  const [lineMode, setLineMode] = useState("free");
  const [tickCount, setTickCount] = useState(10);
  const [rulerColor, setRulerColor] = useState("#ffffff");
  const [onGrid, setOnGrid] = useState(true);

  // for instance variable
  const [showGDSImportModal, setShowGDSImportModal] = useState(false);
  const [x, setX] = useState(0);
  const [y, setY] = useState(0);
  const [selectedFile, setSelectedFile] = useState(null);
  const [showAddCellModal, setShowAddCellModal] = useState(false);
  const [newCellName, setNewCellName] = useState('');


  const [gdsData, setGdsData] = useState(null);
  const [showInstanceForm, setShowInstanceForm] = useState(false);


  const API_BASE_URL = process.env.REACT_APP_API_BASE_URL;

  const exportGDS = async () => {
    const data = { cells: layoutEngine.getCells() };
    const res = await fetch('http://localhost:5000/generate-gds', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(data),
    });

    const blob = await res.blob();
    const url = window.URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = 'layout.gds';
    a.click();
  };

  //shiv code effect for tool change fetch
  useEffect(() => {
  const handleToolChange = (e) => {
    setActiveTool(e.detail); // Update state when event is received
  };

  window.addEventListener("tool-changed", handleToolChange);

  // Set initial tool on load
  setActiveTool(ProjectManager.getTool());

  return () => {
    window.removeEventListener("tool-changed", handleToolChange);
  };
}, []);

  useEffect(() => {
  if (window.canvasEngine) {
    window.canvasEngine.unitScaleFactor = parseFloat(unit);
    window.canvasEngine.rulerLineMode = lineMode;
    window.canvasEngine.rulerUnitSymbol = unit === "1" ? "px" : (unit === "264.583" ? "m" : "nm");
    window.canvasEngine.setRulerLineColor(rulerColor);
    window.canvasEngine.setRulerValues(tickCount);
    window.canvasEngine.rulerOnGrid = onGrid;
  }
}, [unit, lineMode, rulerColor, tickCount, onGrid]);

const handleRulerReset = () => {
  setUnit("1");
  setDrawMode("two");
  setLineMode("free");
  setTickCount(10);
  setRulerColor("#ffffff");
  setOnGrid(true);
  if (window.canvasEngine) {
    window.canvasEngine.allRulers = [];
    window.canvasEngine.draw();
  }
};

 const handleFileUpload = async () => {
    const formData = new FormData();
    formData.append("file", selectedFile);
  
    try {
      const res = await fetch("http://127.0.0.1:5000/upload-gds-instance", {
        method: "POST",
        body: formData,
      });
  
      const data = await res.json();
  
      if (!res.ok) throw new Error(data.detail || "Upload failed");
  
      console.log(" Parsed GDS Result:", data);
  
      // Step 1: Add all cells first
      data.structures.forEach((cell) => {
        layoutEngine.addCell(cell.name);
      });
  
      // Step 2: Add all boundaries as rectangles
      data.structures.forEach((cell) => {
        cell.elements.forEach((el) => {
          if (el.type === "boundary") {
            const x1 = el.xy[0].x;
            const y1 = el.xy[0].y;
            const x2 = el.xy[2].x;
            const y2 = el.xy[2].y;
            layoutEngine.addRectangle(cell.name, x1 * 1000, y1 * 1000, x2 * 1000, y2 * 1000);
          }
        });
      });
  
      // Step 3: Add all SREFs as instances
      data.structures.forEach((cell) => {
        cell.elements.forEach((el) => {
          if (el.type === "sref") {
            const x = el.xy[0].x * 1000;
            const y = el.xy[0].y * 1000;
            const angle = el.angle || 0;
            layoutEngine.addInstance(cell.name, el.sname, x, y, angle, true);
          }
        });
      });
  
      alert(" GDS instance placed!");
      setShowGDSImportModal(false);
      window.canvasEngine.draw();
    } catch (err) {
      console.error(" Upload failed:", err);
      alert("Failed to upload GDS");
    }
  };

    //  NEW util  returns every cell that is currently in memory
const collectAllCells = () => [
  ...layoutEngine.getCells(),          // manually-created
  ...layoutEngine.getUploadedCells()   // GDS-uploaded
];

//  REPLACE old `exportGDS` (or keep it if you still need JSON)
//     with a GDS-download that calls the FastAPI route you already wrote
const downloadGds = async () => {
  const rawCells = collectAllCells();
  if (!rawCells.length) {
    alert("  No layout to download");
    return;
  }

  // Step 1: Format layout cells
  const formattedCells = rawCells.map(cell => {
    const newCell = {
      name: cell.name,
      elements: [],
      instances: []
    };

    // Rectangles
    cell.elements?.forEach(el => {
      if (el.type === "rectangle") {
        const { x, y, width: w, height: h, layer, datatype } = el;
        newCell.elements.push({
          type: "rectangle",
          layer: parseInt(layer),
          datatype: parseInt(datatype) || 0,
          x,
          y,
          width: w,
          height: h
        });
      }
    });

    // Labels
    cell.labels?.forEach(label => {
      newCell.elements.push({
        type: "Label",
        layer_number: parseInt(label.layer),
        datatype_number: parseInt(label.datatype) || 0,
        coordinates: [[label.x, label.y]],
        name: label.text || "",
        rotation: label.rotation || 0
      });
    });

    // Instances
    cell.instances?.forEach(inst => {
      console.log(" Instance object", inst);
      if (!inst.ref) {
        console.warn(" Skipping instance with missing ref:", inst);
        return;
      }
      newCell.instances.push({
        name: inst.ref,
        x: inst.x,
        y: inst.y,
        mirror_x: !!inst.mirrorX,
        rotation: inst.rotation || 0
      });
    });

    return newCell;
  });

  // Step 2: Wrap JSON in tool-compatible format
  const finalJson = {
    data: {
      layout_data: {
        units: {
          user_size: 0.0005,
          db_size: 5e-10
        },
        cells: formattedCells
      }
    }
  };
  

  // Step 3: Send as file upload (FormData + Blob)
  try {
    const blob = new Blob([JSON.stringify(finalJson)], {
      type: "application/json"
    });

    const formData = new FormData();
    formData.append("file", blob, "layout.json");

    const res = await fetch("http://localhost:7000/convert-json-to-gds", {
      method: "POST",
      body: formData
    });

    if (!res.ok) throw new Error("Server responded " + res.status);

    const gdsBlob = await res.blob();
    const url = window.URL.createObjectURL(gdsBlob);

    const a = document.createElement("a");
    a.href = url;
    a.download = `${ProjectManager.getDesignName() || "layout"}.gds`;
    a.click();
    window.URL.revokeObjectURL(url);
  } catch (err) {
    console.error("Download failed:", err);
    alert(" Couldnt download GDS  check console / server logs");
  }
};

// end shiv code



  return (
    <header className="top-bar">
      <div className="logo-and-icons">
        <div className="logo">LOG<b style={{color:"green"}}>IC</b>KNOTS</div>

        

        <div className="icon-bar">
        <Tooltip title="Upload">
            <UploadIcon fontSize="small" onClick={()=>document.getElementById("gds-upload").click()} />
              <input
                type="file"
                id="gds-upload"
                accept=".gds"
                style={{ display: 'none' }}
                onChange={async (e) => {
    const file = e.target.files[0];
    if (!file) return;
  
    setSelectedFile(file);
  
    const formData = new FormData();
    formData.append("file", file);
  
    try {
      const response = await axios.post(`${API_BASE_URL}/upload-gds-instance`, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });
  
      const jsonData = response.data.data.layout_data;

      console.log("Server response:", jsonData);
  
      //  Load cells

     
      //layoutEngine.loadCellsFromJSON(jsonData.cells);

      layoutEngine.loadFromGDS(jsonData.cells);
      //  Select first cell (usually "TOP")
      if (jsonData.cells.length > 0) {
        const topCell = jsonData.cells[0].name;
        ProjectManager.setTopCellName(topCell);         //  required for hierarchy base
        ProjectManager.setSelectedCell(topCell);        //  required for canvas render
        hierarchyManager.addCell(topCell);
        window.canvasEngine.useUploadedCells = true; //  ENABLE GDS RENDERING

      }
    
      //  Redraw canvas
      window.canvasEngine.draw();
  
    } catch (error) {
      console.error("Upload failed:", error);
    }
                }}
  
              />
          </Tooltip>
          <Tooltip title="Download">
            <DownloadIcon fontSize="small" onClick={downloadGds}/>
          </Tooltip>
          <Tooltip title="Add new cell">
            <AddIcon fontSize="small" onClick={() => setShowAddCellModal(true)}/>
          </Tooltip>
          <Tooltip title="Undo">
            <UndoIcon fontSize="small" onClick={() => window.canvasEngine.undo()}/>
          </Tooltip>
          <Tooltip title="Redo">
            <RedoIcon fontSize="small" onClick={() => window.canvasEngine.redo()} />
          </Tooltip>
          <Tooltip title="Copy">
            <ContentCopyIcon fontSize="small" onClick={() => window.canvasEngine.copySelectedObjects()}/>
          </Tooltip>
          <Tooltip title="Paste">
            <NoteAltIcon fontSize="small" onClick={() => window.canvasEngine.pasteCopiedObjects()}/>
          </Tooltip>
          <Tooltip title="Scan Document">
            <DocumentScannerIcon fontSize="small" />
          </Tooltip>
          <Tooltip title="Home">
            <HomeIcon fontSize="small" />
          </Tooltip>
          <Tooltip title="Move">
            <OpenWithIcon fontSize="small" onClick={() => window.canvasEngine.enterMultiMoveMode()}/>
          </Tooltip>
          <Tooltip title="Delete">
            <DeleteIcon fontSize="small" onClick={() => window.canvasEngine.deleteSelectedObjects()}/>
          </Tooltip>
           <Tooltip title="Add Instance">
              <InsertLinkIcon fontSize="small" onClick={() => setShowInstanceForm(true)} />
           </Tooltip>
        </div>
      
          {/* shiv code */}
          {activeTool === 'select' && (
            <div style={{
            display: 'flex',
            gap: '10px',
            alignItems: 'center',
            marginLeft: '20px',
            padding: '5px',
            border: '1px solid #444',
            borderRadius: '8px',
            backgroundColor: '#2b2b2b',
          }}>
            <Tooltip title="Right rotate">
            <RotateRightIcon fontSize="small" style={{ cursor: 'pointer' }} onClick={() => window.dispatchEvent(new CustomEvent('rotate-right'))}/>
          </Tooltip>
          <Tooltip title="Left rotate">
            <RotateLeftIcon fontSize="small" style={{ cursor: 'pointer' }} onClick={() => window.dispatchEvent(new CustomEvent('rotate-left'))} />
          </Tooltip>
          <Tooltip title="Mirror X">
            <FlipIcon fontSize="small" style={{ cursor: 'pointer' }} />
          </Tooltip>
          <Tooltip title="Mirror Y">
            <FlipCameraAndroidIcon fontSize="small" style={{ cursor: 'pointer' }} />
          </Tooltip>
          </div>
          )}
          {activeTool === 'ruler' && (
            <RulerControls
                unit={unit}
                setUnit={setUnit}
                drawMode={drawMode}
                setDrawMode={setDrawMode}
                lineMode={lineMode}
              setLineMode={setLineMode}
              tickCount={tickCount}
              setTickCount={setTickCount}
              onReset={handleRulerReset}
              rulerColor={rulerColor}
              setRulerColor={setRulerColor}
            onGrid={onGrid}
            setOnGrid={setOnGrid}
          />
          )}
      </div>
      <div style={{ marginLeft: 'auto',marginRight: '7px',display: 'flex',alignItems: 'center' }}>
            <input type="text" value={designName} onChange={(e) => {ProjectManager.setDesignName(e.target.value);
            setDesignName(e.target.value);
                }}
    style={{padding: '10px',height:"10px",right:'0px',
      fontSize: '14px',
      borderRadius: '4px',
      border: '1px solid #888',
      backgroundColor: '#222',
      color: '#fff',
      outline: 'none',
      minWidth: '200px'
    }}
    placeholder="Enter Design Name"
            />
      </div>

        {showAddCellModal && (
  <div className="modal" style={{
    background: '#222',
    padding: '20px',
    borderRadius: '8px',
    position: 'fixed',
    top: '30%',
    left: '35%',
    zIndex: 1000,
    color: 'white'
  }}>
    <h3>Create New Cell</h3>
    <input
      type="text"
      placeholder="Enter cell name"
      value={newCellName}
      onChange={(e) => setNewCellName(e.target.value)}
      style={{ padding: '8px', marginBottom: '10px', width: '100%' }}
    />
    <div style={{ display: 'flex', gap: '10px', marginTop: '10px' }}>
      <button onClick={() => {
        if (newCellName.trim()) {
          layoutEngine.addCell(newCellName.trim());
ProjectManager.setSelectedCell(newCellName.trim()); //  Ensure selected

          alert(` Cell '${newCellName}' created`);
          setNewCellName('');
          setShowAddCellModal(false);
          window.canvasEngine.draw(); // redraw canvas
        }
      }}>Create</button>
      <button onClick={() => setShowAddCellModal(false)}>Cancel</button>
    </div>
  </div>
        )}

          {showInstanceForm && (
  <div className="modal" style={{
    background: '#222',
    padding: '20px',
    borderRadius: '8px',
    position: 'fixed',
    top: '30%',
    left: '35%',
    zIndex: 1000,
    color: 'white'
  }}>
    <InstanceInsertForm onClose={() => setShowInstanceForm(false)} />
  </div>
        )}
    </header>
  );
};

export default TopBar;